.container {
  color: blueviolet;
  border: 2px solid rgb(192, 147, 235);
  border-radius: 10px;
  padding: 20px 40px;
  display:flex;
  width: 40rem;
  justify-content: space-between;
  margin-top: 10px;
  box-shadow: 0 10px 80px rgba(0, 0, 0, 0.065);
  margin-left: 27%;
}
.incbtn{
  background-color: rgb(192, 147, 235);
  color: black;
  font-weight: 700;
}
.quantity{
  border-radius: 50%
}
input{
  border: 2px solid rgb(192, 147, 235);
  border-radius: 10px;
  height: 25px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  color: blueviolet;
}
.remove{
  color: red;
  border: 2px solid red;
}