.App {
  text-align: center;
  width: 100%;
  height: 1000px;
  min-height: 100vh;
  justify-self: center;
  justify-content: center;
  background-color: rgb(240, 230, 249);
}
h1 {
  color: blueviolet;
}
button {
  color: rgb(113, 37, 184);
  height: 30px;
  margin: 10px;
  border-radius: 10px;
  border: 2px solid rgb(192, 147, 235);
  background-color: rgb(241, 212, 255);
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  letter-spacing: 0.7px;
  font-weight: 400;
  cursor: pointer;
}

